import React, { FC } from 'react'
import { Button, Space, Tooltip } from 'antd'
import {
  BlockOutlined,
  CopyOutlined,
  DeleteOutlined,
  DownOutlined,
  EyeInvisibleOutlined,
  LockOutlined,
  RedoOutlined,
  UndoOutlined,
  UpOutlined,
} from '@ant-design/icons'
import { useDispatch } from 'react-redux'
import { ActionCreators as UndoOrRedoActionCreators } from 'redux-undo'
import {
  changeComponentHidden,
  removeSelectedComponent,
  toggleComponentLocked,
  copySelectedComponent,
  pasteCopiedComponent,
  moveComponent,
} from '@/store/componentsReducer'
import useGetComponentInfo from '@/hooks/useGetComponentInfo'

const EditTool: FC = () => {
  const dispatch = useDispatch()
  const {
    selectedId,
    selectedComponent,
    copiedComponents,
    componentList = [],
  } = useGetComponentInfo() // è·å–ç»„ä»¶çš„ä¿¡æ¯
  const { isLocked } = selectedComponent || {}
  const selectedIndex = componentList.findIndex(c => c.id === selectedId)
  const isFirst = selectedIndex <= 0 // æ˜¯ç¬¬ä¸€ä¸ª
  const isLast = selectedIndex >= componentList.length - 1

  function handleDelete() {
    // åˆ é™¤ç»„ä»¶
    dispatch(removeSelectedComponent())
  }
  function handleHidden() {
    // éšè—ç»„ä»¶
    dispatch(changeComponentHidden({ id: selectedId, isHidden: true }))
  }
  // é”å®šç»„ä»¶
  function handleLock() {
    dispatch(toggleComponentLocked({ id: selectedId }))
  }
  // å¤åˆ¶
  function handleCopy() {
    dispatch(copySelectedComponent())
  }
  // ç²˜è´´
  function handlePaste() {
    dispatch(pasteCopiedComponent())
  }
  // ä¸Šç§»
  function handleMoveUp() {
    if (isFirst) return
    dispatch(moveComponent({ oldIndex: selectedIndex, newIndex: selectedIndex - 1 }))
  }
  // ä¸‹ç§»
  function handleMoveDown() {
    if (isLast) return
    dispatch(moveComponent({ oldIndex: selectedIndex, newIndex: selectedIndex + 1 }))
  }
  // æ’¤é”€
  function handleUndo() {
    dispatch(UndoOrRedoActionCreators.undo())
  }
  function handleRedo() {
    dispatch(UndoOrRedoActionCreators.redo())
  }
  return (
    <div>
      <Space>
        <Tooltip title="åˆ é™¤">
          <Button shape="circle" icon={<DeleteOutlined />} onClick={handleDelete} />
        </Tooltip>
        <Tooltip title="éšè—">
          <Button shape="circle" icon={<EyeInvisibleOutlined />} onClick={handleHidden} />
        </Tooltip>
        <Tooltip title="é”å®š">
          <Button
            shape="circle"
            icon={<LockOutlined />}
            onClick={handleLock}
            type={isLocked ? 'primary' : 'default'}
          />
        </Tooltip>
        <Tooltip title="å¤åˆ¶">
          <Button shape="circle" icon={<CopyOutlined />} onClick={handleCopy} />
        </Tooltip>
        <Tooltip title="ç²˜è´´">
          <Button
            shape="circle"
            icon={<BlockOutlined />}
            onClick={handlePaste}
            disabled={copiedComponents === null}
          />
        </Tooltip>
        <Tooltip title="ä¸Šç§»">
          <Button shape="circle" icon={<UpOutlined />} onClick={handleMoveUp} disabled={isFirst} />
        </Tooltip>
        <Tooltip title="ä¸‹ç§»">
          <Button
            shape="circle"
            icon={<DownOutlined />}
            onClick={handleMoveDown}
            disabled={isLast}
          />
        </Tooltip>
        <Tooltip title="æ’¤é”€">
          <Button shape="circle" icon={<UndoOutlined />} onClick={handleUndo} />
        </Tooltip>
        <Tooltip title="é‡åš">
          <Button shape="circle" icon={<RedoOutlined />} onClick={handleRedo} />
        </Tooltip>
      </Space>
    </div>
  )
}

export default EditTool
