import { useKeyPress } from 'ahooks'
import { useDispatch } from 'react-redux'
import { ActionCreators as UndoOrRedoActionCreators } from 'redux-undo'
import {
  copySelectedComponent,
  removeSelectedComponent,
  pasteCopiedComponent,
  selectPrevComponent,
  selectNextComponent,
} from '../store/componentsReducer'

/**
 * åˆ¤æ–­activeElementæ˜¯å¦åˆæ³•
 */
const isActiveElementValid = () => {
  const activeElement = document.activeElement
  // æ²¡æœ‰å¢åŠ dnd-kitä¹‹å‰
  //if (activeElement === document.body) return true

  // å¢åŠ äº†dnd-kitä¹‹å
  if (activeElement === document.body) return true
  if (activeElement?.matches('div[role="button"]')) return true
  return false
}

const useBindCanvasKeyPress = () => {
  const dispatch = useDispatch()

  // åˆ é™¤ç»„ä»¶
  useKeyPress(['backspace', 'delete'], () => {
    if (!isActiveElementValid()) return
    dispatch(removeSelectedComponent())
  })

  // å¤åˆ¶
  useKeyPress(['ctrl.c', 'meta.c'], () => {
    if (!isActiveElementValid()) return
    dispatch(copySelectedComponent())
  })

  // ç²˜è´´
  useKeyPress(['ctrl.v', 'meta.v'], () => {
    if (!isActiveElementValid()) return
    dispatch(pasteCopiedComponent())
  })

  // é€‰ä¸­ä¸Šä¸€ä¸ª
  useKeyPress('uparrow', () => {
    dispatch(selectPrevComponent())
  })

  // é€‰ä¸­ä¸‹ä¸€ä¸ª
  useKeyPress('downarrow', () => {
    dispatch(selectNextComponent())
  })

  //æ’¤é”€
  useKeyPress(
    ['ctrl.z', 'meta.z'],
    () => {
      dispatch(UndoOrRedoActionCreators.undo())
    },
    {
      exactMatch: true,
    }
  )
  // é‡åš
  useKeyPress(
    ['ctrl.shift.z', 'meta.shift.z'],
    () => {
      dispatch(UndoOrRedoActionCreators.redo())
    },
    {
      exactMatch: true,
    }
  )
}

export default useBindCanvasKeyPress
