import React, { FC, MouseEvent } from 'react'
import { Spin } from 'antd'
import { useDispatch } from 'react-redux'
import classNames from 'classnames'
import styles from './index.module.scss'
import useGetComponentInfo from '@/hooks/useGetComponentInfo'
import { getComponentConfByType } from '@/components/QuestionComponents'
import useBindCanvasKeyPress from '@/hooks/useBindCanvasKeyPress'
import SortableContainer from '@/components/DragSortable/SortableContainer'
import SortableItem from '@/components/DragSortable/SortableItem'
import { ComponentInfoType, changeSelectedId, moveComponent } from '@/store/componentsReducer'

type PropsType = {
  loading: boolean
}

// ç”Ÿæˆç»„ä»¶
function genComponent(component: ComponentInfoType) {
  const { type, props } = component
  // é€šè¿‡ç»„ä»¶ç±»å‹è·å¾—ç»„ä»¶å®ä¾‹
  const componentCof = getComponentConfByType(type)
  // æ‰¾ä¸åˆ°
  if (!componentCof) return null

  // æ‹¿åˆ°å½“å‰typeæ‰€å¯¹åº”çš„ç»„ä»¶å®ä¾‹
  const { Component } = componentCof
  // è¿”å›ç»„ä»¶å®ä¾‹ ä»¥åŠå‘é‡Œé¢ä¼ å…¥ç›¸å¯¹åº”çš„æ•°æ® å¦‚æœæ²¡æœ‰åˆ™æ˜¾ç¤ºé»˜è®¤æ•°æ®
  return <Component {...props} />
}

const EditCanvas: FC<PropsType> = (props: PropsType) => {
  const { loading } = props
  // è·å¾—åç«¯è¿”å›çš„ç»„ä»¶åˆ—è¡¨
  const { componentList, selectedId } = useGetComponentInfo()
  const dispatch = useDispatch()

  function handleClick(event: MouseEvent, id: string) {
    // å› ä¸ºæˆ‘ä»¬éœ€è¦ç‚¹å‡»åˆ°çˆ¶å…ƒç´ ä¸Šæ—¶å–æ¶ˆé€‰ä¸­ ä½†æ˜¯ç”±äºäº‹ä»¶å†’æ³¡ä¼šä¼ ç»™çˆ¶å…ƒç´ 
    event.stopPropagation()
    dispatch(changeSelectedId(id))
  }
  // ç»‘å®šå¿«æ·é”®
  useBindCanvasKeyPress()
  // é¡µé¢åŠ è½½æ—¶ æ˜¾ç¤ºçš„loading
  if (loading) {
    return (
      <div style={{ textAlign: 'center', marginTop: '24px' }}>
        <Spin />
      </div>
    )
  }
  // ç»“æŸæ‹–æ‹½æ—¶
  function handleDragEnd(oldIndex: number, newIndex: number) {
    dispatch(moveComponent({ oldIndex, newIndex }))
    //console.log('handleDragEnd', oldIndex, newIndex)
  }
  // å› ä¸ºSortableContaineréœ€è¦æ•°æ®é¡¹ä¸­éƒ½æœ‰id
  const componentListWithId = componentList.map(item => {
    return {
      ...item,
      id: item.id,
    }
  })
  return (
    <SortableContainer items={componentListWithId} onDragEnd={handleDragEnd}>
      <div className={styles.canvas}>
        {/* è¿‡æ»¤æ‰è¦éšè—çš„ç»„ä»¶ */}
        {componentList
          .filter(c => !c.isHidden)
          .map(c => {
            const { id, isLocked } = c
            // æ‹¼æ¥class name
            // è®¾ç½®é»˜è®¤çš„ç±»å
            const wrapperDefaultClassName = styles['component-wrapper']
            // è®¾ç½®é€‰ä¸­æ—¶çš„ç±»å
            const selectedClassName = styles.selected
            const lockedClassName = styles.locked
            // åˆ¤æ–­å½“å‰çš„idæ˜¯å¦ç­‰äºé€‰ä¸­çš„idï¼Œå¦‚æœç›¸ç­‰åˆ™è®¾ç½®é€‰ä¸­æ—¶çš„ç±»åï¼Œå¦åˆ™è®¾ç½®é»˜è®¤çš„ç±»å
            const wrapperClassName = classNames({
              [wrapperDefaultClassName]: true,
              [selectedClassName]: id === selectedId,
              [lockedClassName]: isLocked,
            })
            return (
              <SortableItem key={id} id={id}>
                <div key={id} className={wrapperClassName} onClick={e => handleClick(e, id)}>
                  <div className={styles.component}>{genComponent(c)}</div>
                </div>
              </SortableItem>
            )
          })}
      </div>
    </SortableContainer>
  )
}

export default EditCanvas
